form{
    margin:16dvb;
    background-color:azure ;
    border: solid black 1px;
    border-radius: 4px;
    padding: 30px;
    width: min-content;
}
body{
    background-color: rgb(170, 209, 209);
}

.head{
    font-size: xx-large;
    background-color: rgb(34, 19, 102);
    color:white;
}
input{
    border-radius: 6px;
    border: solid green 1px;
    margin: 10px;
    cursor: pointer;
}
input[type=submit]{
    background-color: blue;
    color: white;
}
input[type=button]{
    background-color: rgb(170, 209, 209);
    border: none;
    font-size: medium;
    font-weight:bold;
    color: blue;
}
button{
    border-radius: 2px;
    padding: 8px;
    border:solid black 1px;
    background-color:blue;
    color: aliceblue;
    cursor: pointer;
}
